:root {
  --font: "Azeret Mono", monospace;
}

h1 {
  font-size: 3rem;
  color: var(--font-colour);
}

li {
  list-style: none;
  color: var(--font-colour);
  padding-bottom: 0.5rem;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: var(--font);
}

body {
  min-height: 100vh;
  background: var(--bg);
  background-repeat: no-repeat;
}

:root {
  --bg: linear-gradient(to bottom right, #718e75, #8da591);
  --font-colour: #804577;
}

.done {
  text-decoration: line-through;
  opacity: 0.5;
}

.dissapear {
  opacity: 0;
  transform: rotatex(90deg);
}

.header,
.form {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 15vh;
}

.input input {
  width: 70%;
}
.input input, .input button {
  margin: 0;
  padding: 0.3rem;
  font-size: 2rem;
  color: var(--font-colour);
  border: none;
  outline: none;
  background: white;
}
@media (max-width: 700px) {
  .input input, .input button {
    font-size: 1rem;
  }
}
.input button {
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.input button:hover {
  color: #718e75;
}

.todo-conatianer,
.todo-list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.todo {
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  background: #718e75;
  padding: 0.5rem;
  margin-bottom: 0.5rem;
  transition: all 0.5s ease-in-out;
}
.todo li {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  font-weight: 700;
  flex: 1;
  padding-right: 1rem;
  width: 80%;
}
@media (max-width: 700px) {
  .todo li {
    font-size: 1rem;
  }
}
.todo .completed-btn, .todo .trash-btn {
  font-size: 1.5rem;
  border: none;
  outline: none;
  padding: 0.5rem;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  background: var(--font-colour);
  color: white;
}
@media (max-width: 700px) {
  .todo .completed-btn, .todo .trash-btn {
    font-size: 1rem;
  }
}
.todo .completed-btn:hover, .todo .trash-btn:hover {
  background: transparent;
  color: var(--font-colour);
}

.fa-trash,
.fa-check {
  pointer-events: none;
  cursor: pointer;
}